import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import Stack from '@mui/material/Stack';
import CardMedia from '@mui/material/CardMedia';
import three from '../static/images/3.jpg';

function AboutCard({ title, content }) {
  return (
    <Card
      raised
      sx={{
        minWidth: 210, maxWidth: 400, py: 1, m: 1, maxHeight: 400,
      }}
    >
      <CardHeader
        title={title}
        sx={{
          backgroundColor: 'primary.main',
          textAlign: 'center',
          color: 'button.main',
        }}
      />
      <CardContent sx={{ maxHeight: '260px', overflowY: 'auto', textRendering: 'optimizeLegibility' }}>
        <Typography sx={{ textAlign: 'left', color: 'faded.black' }}>
          {content}
        </Typography>
      </CardContent>
    </Card>
  );
}

function About() {
  return (
    <Grid container justifyContent="center" alignItems="flex-start">
      <Stack direction="column">
        <AboutCard
          title="Mission"
          content="To make a home worthy of your timeless, priceless memories."
        />
        <AboutCard
          title="What we are not"
          content="A photo graveyard app. Where photos are dumped mindlessly, never to be visited again."
        />
      </Stack>
      <Grid item align="center">
        <AboutCard
          title="Our Story"
          content="When our son was born 2 years back, we started clicking pictures like crazy.
              Sadly enough could not find them when related conversations arose.
              Most photo storage solutions out there felt like empty boxes where you dump all photos.
              We kept the most precious photos along with the misclicks, the hazy ones and the ones
              we could not remember why we took.
              We kept the photos but what we really wanted to keep were the memories.
              But what are photos without the associated emotions and the stories.
              That is why we started building myMemoryStore, where you can keep your precious photos and
              the stories related to them so that you can relive the memories whenever you want."
        />
      </Grid>
      <Grid item align="center">
        <Card sx={{
          minWidth: 210, maxWidth: 400, py: 1, m: 1, maxHeight: 400, backgroundColor: 'transparent',
        }}
        >
          <CardHeader
            title="The Team"
            sx={{
              backgroundColor: 'primary.main',
              textAlign: 'center',
              color: 'button.main',
            }}
          />
          <CardMedia image={three} sx={{ height: 300 }} title="three" />
        </Card>
      </Grid>
    </Grid>
  );
}

AboutCard.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
};

export default About;
